.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__info {
  margin: 40px 0 0;
  padding: 0;
}

.card__name {
  color: #060b35;
  text-underline-position: from-font;
  width: 100%;
  height: 36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  text-underline-position: from-font;
  margin: 4px 0 16px;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__money {
  justify-content: space-between;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price {
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__numb {
  text-align: right;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  background-color: #00acdc;
  border-color: #00acdc;
  border-radius: 5px;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
}

.card__button-link {
  color: #fff;
  text-underline-position: from-font;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  text-align: right;
  color: #060b35;
  text-underline-position: from-font;
  width: 100%;
  height: 16px;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}
